"use client";

import { useState } from "react";

export default function AskPage() {
  const [question, setQuestion] = useState("");
  const [answer, setAnswer] = useState(null);
  const [context, setContext] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleAsk = async () => {
    if (!question.trim()) return;
    setLoading(true);
    setAnswer(null);
    setContext([]);

    try {
      const res = await fetch("http://localhost:8000/ask", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ question, top_k: 3 }),
      });

      if (!res.ok) {
        throw new Error("Failed to fetch from backend");
      }

      const data = await res.json();
      setAnswer(data.answer);
      setContext(data.context);
    } catch (err) {
      console.error(err);
      setAnswer("Error: Could not fetch answer from backend.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-6">
      <h1 className="text-3xl font-bold mb-6">Ask your PDF Bot</h1>

      <div className="w-full max-w-xl bg-white shadow-md rounded-lg p-6">
        <textarea
          className="w-full border rounded-lg p-3 mb-4"
          rows={3}
          placeholder="Type your question..."
          value={question}
          onChange={(e) => setQuestion(e.target.value)}
        />

        <button
          onClick={handleAsk}
          className="w-full bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 disabled:bg-gray-400"
          disabled={loading}
        >
          {loading ? "Thinking..." : "Ask"}
        </button>

        {answer && (
          <div className="mt-6">
            <h2 className="text-xl font-semibold">Answer:</h2>
            <p className="mt-2 p-3 bg-gray-50 rounded-lg">{answer}</p>

            {context.length > 0 && (
              <div className="mt-4">
                <h3 className="text-lg font-semibold">Context:</h3>
                <ul className="list-disc list-inside mt-2 space-y-2">
                  {context.map((c, idx) => (
                    <li key={idx} className="bg-gray-100 p-2 rounded-lg">
                      {c}
                    </li>
                  ))}
                </ul>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
